<template>
  <div>
    <div
      class="
        container
        bg-clip-padding
        backdrop-filter backdrop-blur-xl
        bg-opacity-30
        border border-gray-200
        bg-gray-400
        w-full
        md:w-1/2
        mx-auto
        p-5
        rounded
        shadow-sm
      "
    >
      <h2 class="mx-auto text-center text-2xl mb-3 font-semibold text-white">
        Identify Yourself
      </h2>
      <form ref="form" class="space-y-3">
        <div>
          <label for="" class="label-style text-white">Select Course</label>
          <select
            required
            v-model="course"
            class="
              block
              w-full
              rounded-md
              border-transparent
              focus:border-gray-500
              focus:bg-white
              focus:ring-0
            "
          >
            <option v-for="(o, i) in authoptions" :key="i" :value="o.value">
              {{ o.text }}
            </option>
          </select>
        </div>
        <div class="my-2">
          <p class="text-center kalpurush text-lg text-white">
            {{
              this.course == "mathhaters"
                ? "Math Haters Math Course"
                : "Apar Chinmoy Engineering Foundation           Batch"
            }}
            এ টাকা পাঠানোর সময় যে
            {{ this.course == "mathhaters" ? "ফোন নম্বর" : "ইমেইল" }} ব্যবহার
            করেছিলে এইখানেও একই
            {{ this.course == "mathhaters" ? "ফোন নম্বর" : "ইমেইল" }} ব্যবহার
            করে প্রবেশ করবে।
          </p>
        </div>
        <div v-if="this.course == 'aparchinmoy'">
          <label class="label-style text-white">E-mail</label>
          <input
            required
            type="email"
            class="input-block"
            v-model="cred"
            placeholder="eg. example@example.com"
          />
        </div>
        <div v-else>
          <label class="label-style text-white">Phone</label>
          <input
            required
            v-model="phone"
            type="text"
            class="input-block"
            placeholder="eg. 01XXXXXXXXX"
          />
        </div>
        <div v-if="this.course == 'aparchinmoy'">
          <label class="label-style text-white">Group Joining ID</label>
          <input
            required
            type="password"
            class="input-block"
            v-model="password"
            placeholder="*******"
          />
        </div>
        <div class="space-y-3" v-if="unknown">
          <div>
            <label class="label-style text-white">Name</label>
            <input
              required
              type="text"
              class="input-block"
              v-model="name"
              placeholder="Write your name"
            />
          </div>
          <div>
            <label class="label-style text-white">College</label>
            <input
              required
              type="text"
              class="input-block"
              v-model="school"
              placeholder="College Name"
            />
          </div>

          <div v-if="course == 'aparchinmoy'">
            <label class="label-style text-white">Phone</label>
            <input
              required
              v-model="phone"
              type="text"
              class="input-block"
              placeholder="eg. 01XXXXXXXXX"
            />
          </div>
          <div>
            <label for="" class="label-style text-white">Gender</label>
            <select
              required
              v-model="gender"
              class="
                block
                w-full
                rounded-md
                border-transparent
                focus:border-gray-500
                focus:bg-white
                focus:ring-0
              "
            >
              <option value="Male">Male</option>
              <option value="Female">Female</option>
            </select>
          </div>
          <div>
            <label for="" class="label-style text-white">Division</label>
            <select
              required
              v-model="division"
              class="
                block
                w-full
                rounded-md
                border-transparent
                focus:border-gray-500
                focus:bg-white
                focus:ring-0
              "
              placeholder="Select Division"
            >
              <option v-for="(d, i) in divisions" :key="i" :value="d">
                {{ d }}
              </option>
            </select>
          </div>
        </div>
        <div class="pt-2 pb-2 text-center" v-if="this.course == 'aparchinmoy'">
          <router-link
            to="/resetpass"
            class="
              font-semibold
              rounded
              border border-red-500
              text-white
              bg-white
              px-3
              py-2
              text-sm
              bg-gradient-to-r
              from-red-400
              to-red-600
              kalpurush
            "
            >পাসওয়ার্ড ভুলে গেলে এখানে ক্লিক করো</router-link
          >
        </div>
        <div
          class="mt-4 bg-red-600 text-white rounded px-2 py-3"
          v-if="message"
        >
          {{ message }}
        </div>
        <div
          class="mt-4 bg-red-600 text-white rounded px-2 py-3"
          dark
          v-if="this.$store.state.error"
        >
          {{ this.$store.state.error }}
        </div>
        <div class="text-center">
          <button
            type="button"
            v-if="unknown"
            @click="submitInformation"
            class="
              px-5
              py-3
              bg-gradient-to-r
              from-red-400
              to-red-600
              font-semibold
              text-white
              rounded
              flex
              mx-auto
              hover:border-red-900
              border
            "
          >
            <div
              v-if="loading"
              class="
                w-6
                h-6
                mx-1
                border-4 border-t-4 border-white
                rounded-full
                animate-spin
                loader_btn
              "
            />
            {{ loading ? "Loading..." : "Submit" }}
          </button>
          <button
            type="button"
            v-else
            @click="checkPhoneNumber"
            class="
              px-5
              py-3
              bg-gradient-to-r
              from-red-400
              to-red-600
              font-semibold
              text-white
              rounded
              flex
              mx-auto
              hover:border-red-900
              border
            "
          >
            <div
              v-if="loading"
              class="
                w-6
                h-6
                mx-1
                border-4 border-t-4 border-white
                rounded-full
                animate-spin
                loader_btn
              "
            />
            {{ loading ? "Loading..." : "Submit" }}
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { auth } from "@/plugins/config.js";
export default {
  data() {
    return {
      authoptions: [
        { text: "Apar Chinmoy Physics Course", value: "aparchinmoy" },
        { text: "Math Haters Math Course", value: "mathhaters" },
      ],
      course: "aparchinmoy",
      valid: true,
      loading: false,
      phone: "",
      name: "",
      cred: "",
      gender: "Male",
      school: "",
      division: "Dhaka",
      password: "",
      unknown: false,
      message: "",
      divisions: [
        "Dhaka",
        "Mymensingh",
        "Chattagram",
        "Rajshahi",
        "Barishal",
        "Syhlet",
        "Rangpur",
        "Khulna",
      ],
    };
  },
  methods: {
    checkPhoneNumber() {
      if (this.$refs.form.checkValidity()) {
        this.loading = !this.loading;
        if (this.course == "mathhaters") {
          fetch(
            "https://script.google.com/macros/s/AKfycbxkUJ0ib6MbkwYUnnOEJgmiKQknB8zauTrrQQuA89o1mZP7iuoJkj_fdlzlnHzmRuHs/exec?phone=" +
              this.phone
          )
            .then((res) => res.json())
            .then((data) => {
              let cred = this.phone.substr(this.phone.length - 10) + "@ac.com";
              if (data.message == "exists") {
                fetch(
                  auth +
                    `?phone=${this.phone.substr(
                      this.phone.length - 10
                    )}&check=phone`
                )
                  .then((res) => res.json())
                  .then((data) => {
                    this.loading = !this.loading;
                    if (data.message == "exists") {
                      localStorage.setItem("acuser", cred);
                      this.$router.push("/");
                    } else {
                      this.unknown = !this.unknown;
                    }
                  });
              } else {
                this.loading = !this.loading;
                this.message = "Doesn't exists.";
              }
            });
        } else {
          fetch(auth + `?email=${this.cred}&check=email`)
            .then((res) => res.json())
            .then((data) => {
              this.loading = !this.loading;
              if (data.message == "exists") {
                this.login();
              } else {
                this.unknown = !this.unknown;
              }
            });
        }
      }
    },
    submitInformation() {
      if (this.$refs.form.checkValidity()) {
        let data = localStorage.getItem("acuser");
        if (!data) {
          this.loading = !this.loading;
          let cred =
            this.course == "aparchinmoy"
              ? this.cred
              : this.phone.substr(this.phone.length - 10) + "@ac.com";
          fetch(auth, {
            method: "POST",
            cache: "no-cache",
            redirect: "follow",
            body: JSON.stringify([
              cred,
              this.name,
              this.school,
              this.phone.substr(this.phone.length - 10),
              this.gender,
              this.division,
            ]),
          })
            .then((res) => res.json())
            .then(() => {
              if (this.course == "mathhaters") {
                localStorage.setItem("acuser", cred);
                this.$router.push("/");
              } else {
                this.login();
              }
              this.loading = !this.loading;
            });
        }
      }
    },
    login() {
      if (/[\d]{11}/g.test(this.cred) && this.password) {
        this.$store.commit("SET_ERROR", "");
        let userdata = {
          cred: this.cred + "@asg.com",
          password: this.password,
        };
        this.$store.dispatch("userLogin", userdata);
      } else if (
        /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i.test(
          this.cred
        ) &&
        this.password
      ) {
        this.$store.commit("SET_ERROR", "");

        let userdata = {
          cred: this.cred,
          password: this.password,
        };
        this.$store.dispatch("userLogin", userdata);
      } else {
        this.$store.commit("SET_ERROR", "Please provide valid credential.");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.numberbg {
  background: rgba(255, 255, 255, 0.7) !important;
  border-radius: 35px;
}
.loader_btn {
  border-top-color: rgb(77, 75, 75) !important;
}
</style>